import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';

import { DataService } from '../share/data.service';
import { donhang } from '../models/donhang';

@Component({
  selector: 'app-xem-don-hang',
  templateUrl: './xem-don-hang.component.html',
  styleUrls: ['./xem-don-hang.component.css']
})
export class XemDonHangComponent implements OnInit {
  donhangs: donhang[];
state ="";
  constructor(private route: ActivatedRoute, private dataService: DataService) {
    this.dataService.getDonhangs().subscribe(donhangs => {
      this.donhangs = donhangs;
      console.log(this.donhangs);
    });
    this.route.paramMap.subscribe((param: ParamMap) => {
      this.state = param.get('state');
      return this.state;
    });
  }

  ngOnInit() {
  }

}



