import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { DataService } from '../share/data.service';
import { donhang } from '../models/donhang';
import { bk } from '../models/bk';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { DialogComponent } from '../dialog/dialog.component';
import { UploadService } from '../share/upload.service';
import { sign } from '../models/sign';
import { Observable } from 'rxjs/Observable';
import * as firebase from 'firebase';
import { Url } from 'url';
@Component({
  selector: 'app-view-detail',
  templateUrl: './view-detail.component.html',
  styleUrls: ['./view-detail.component.css'],
  inputs: ['state']
})
export class ViewDetailComponent implements OnInit {
  url: Url = {};
  donhang: donhang;
  bk: bk[];
  donhangs: donhang[];
  filters: { name: string; count: number; }[];
  id = "";
  imageUrl = '';

  states = ["Đã giao", "Đang đi giao", "Đang bốc hàng", "Chưa giao"];
  selected = "";
  //sign = false;
  isvisibleBk = false;
  editstate = false;
  inpstate = false;

  editkh = false;
  inpkh = false;

  edittt = false;
  inptt = false;

  editgh = false;
  inpgh = false;

  editbk = false;
  itemtoEdit: bk;
  inpbk = false;
  images: Observable<sign[]>;

  @ViewChild("imgsign") imgsign;
  constructor(private dataService: DataService, private route: ActivatedRoute, public dialog: MatDialog, private uploadService: UploadService) {
    this.route.paramMap.subscribe((param: ParamMap) => {
      this.id = param.get('id');
      console.log('id', this.id);
      return this.id;
    });


    // console.log(this.uploadService.getImages(this.id, this.url));

  }
  // ngAfterViewInit() {

  //   var storage = firebase.storage().ref();
  //   var path = "uploads/" + this.id + ".png";
  //   var spaceRef = storage.child(path);

  //   storage.child(path).getDownloadURL()
  //     .then(function (url) {
  //       console.log("url",url);
  //       // let img = this.imgsign.nativeElement;
  //       // img.src = url;
  //      var test = url;
  //       (<HTMLImageElement>document.getElementById('imgsign')).src = test;

  //       //this.url = url;
  //     // console.log("this url",this.url);
  //     }).catch(function (error) {
  //       console.log(error);
  //     });

  // }
  ngOnInit() {
    // this.getImageUrl();
    this.dataService.getBk(this.id).subscribe(bk => {
      this.bk = bk;
    });
    this.dataService.getDonhangs().subscribe(donhangs => {
      this.donhangs = donhangs;

      for (let i = 0; i < donhangs.length; i++) {
        if (donhangs[i].trangthai == "Đã giao" && donhangs[i].id == this.id) {
          var storage = firebase.storage().ref();
          var path = "uploads/" + this.id + ".png";
          var spaceRef = storage.child(path);

          storage.child(path).getDownloadURL()
            .then(url => {
              console.log("url", url);
              // let img = this.imgsign.nativeElement;
              // img.src = url;
              //var test = url;
              //(<HTMLImageElement>document.getElementById('imgsign')).src = test;

              this.url = url;
              // console.log("this url",this.url);
            }).catch(function (error) {
              console.log(error);
            });

        }
      }
    });

  }
  edit(donhang) {

    console.log("donhang update", donhang);
    if (this.editstate) {
      console.log("selected", this.selected)
      donhang.trangthai = this.selected;
      this.dataService.updateDh(donhang);
    }
    else {
      this.selected = donhang.trangthai;
    }
    this.inpstate = !this.inpstate;
    this.editstate = !this.editstate;

    console.log(this.editstate);
  }
  editKh(donhang: donhang) {
    if (this.editkh) {
      this.dataService.updateDh(donhang);
    }
    this.inpkh = !this.inpkh;
    this.editkh = !this.editkh;


  }
  editBk(donhang: donhang, bk: bk) {
    console.log("donhang", donhang);
    console.log("bk", bk);
    donhang.tong = 0;

    if (this.editbk) {

      bk.thanhtien = bk.mathang.dongia * bk.soluong;
      this.dataService.updatebk(bk);

    }
    else {
      this.itemtoEdit = bk;
    };
    for (let i = 0; i < this.bk.length; i++) {
      if (this.bk[i].id == donhang.id) {
        donhang.tong += this.bk[i].thanhtien;
      }
    };

    donhang.nolai = donhang.tong - donhang.thanhtoan;
    this.dataService.updateDh(donhang);
    this.inpbk = !this.inpbk;
    this.editbk = !this.editbk;
  }
  checkDel(bk: bk): void {
    let dialogRef = this.dialog.open(DialogComponent, {
      width: '250px',
      data: { tenhang: bk.mathang.tenhang }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.dataService.removeBk(bk);
      }
    });
  }

  editTt(donhang: donhang) {
    if (this.edittt) {

      donhang.nolai = donhang.tong - donhang.thanhtoan;
      this.dataService.updateDh(donhang);

    }
    else {

    };
    this.edittt = !this.edittt;
    this.inptt = !this.inptt;
  }
  editGh(donhang: donhang) {
    if (this.editgh) {
      this.dataService.updateDh(donhang);
    }
    this.editgh = !this.editgh;
    this.inpgh = !this.inpgh;
  }

  getImageUrl() {
    console.log("get img");
    var storage = firebase.storage();
    var ref = "uploads/" + this.id + ".png";
    var bkRef = storage.ref(ref).getDownloadURL()
      .then(function (url) {
        console.log("url", url);
        let img = this.imgsign.nativeElement;
        img.src = url;
        // this.url = url;
        console.log("this url", this.url);
      }).catch(function (error) {
        console.log(error);
      });


  };

}








