import { BrowserModule } from '@angular/platform-browser';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { RouterModule} from '@angular/router';
import { CommonModule} from '@angular/common';
import { FormsModule} from '@angular/forms';
import {NoopAnimationsModule} from '@angular/platform-browser/animations';

import {MatToolbarModule, MatMenuModule,MatButtonModule, 
        MatCheckboxModule, MatIconModule, MatTabsModule,
        MatCardModule, MatPaginatorModule, MatFormFieldModule, 
        MatFormFieldControl, MatListModule, MatGridListModule,
        MatInputModule, MatSelectModule, MatDialogModule} from '@angular/material';

import { AngularFireModule } from 'angularfire2';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireDatabase} from 'angularfire2/database';
import { environment } from '../environments/environment';

import {AuthenticationGuard} from './share/authenticationGuard.service';
import {AuthenticationService} from './share/authentication.service';
import { DataService } from './share/data.service';
import { UploadService } from './share/upload.service';
import { ReversePipe } from './share/reverse.pipe';
import { AppComponent } from './app.component';
import { ViewComponent } from './view/view.component';
import { XemDonHangComponent } from './xem-don-hang/xem-don-hang.component';
import { ViewDetailComponent } from './view-detail/view-detail.component';
import { DialogComponent } from './dialog/dialog.component';
import { SignComponent } from './sign/sign.component';
import { LoginComponent } from './login/login.component';
import { TaDonHangComponent } from './ta-don-hang/ta-don-hang.component';


@NgModule({
  declarations: [
    AppComponent,
    ViewComponent,
    XemDonHangComponent,  
    ViewDetailComponent,
    DialogComponent, 
    SignComponent, 
    LoginComponent, TaDonHangComponent,
    
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    AngularFireModule.initializeApp(environment.firebase),
    AngularFirestoreModule,
    AngularFireDatabaseModule,
    AngularFireAuthModule,
    CommonModule,
    FormsModule,
    NoopAnimationsModule,
    MatButtonModule, 
    MatCheckboxModule,
    MatMenuModule,
    MatIconModule,
    MatToolbarModule,
    MatTabsModule,
    MatCardModule,
    MatListModule,
    MatGridListModule,  
    MatPaginatorModule,
    MatFormFieldModule,
    MatInputModule,
    MatSelectModule,
    MatDialogModule,
    RouterModule.forRoot([
     {path: 'login', component: LoginComponent },
     {path: 'xem-don-hang/:state/tao-don-hang', component: TaDonHangComponent },
      { path: 'xem-don-hang/:state', component: XemDonHangComponent },
      { path: 'xem-don-hang/:state/xem-chi-tiet/:id', component: ViewDetailComponent, canActivate:[AuthenticationGuard] },
      { path: 'xem-don-hang/:state/xem-chi-tiet/:id/sign', component: SignComponent, canActivate:[AuthenticationGuard]},
      { path: '', redirectTo: 'login', pathMatch: 'full' },
    
    ]),
  ],
  providers: [DataService,UploadService,
    AuthenticationService,AuthenticationGuard,
    ReversePipe],
  bootstrap: [AppComponent, DialogComponent]
})
export class AppModule { }



