import { Component } from '@angular/core';
import { ViewChild } from "@angular/core";
import { AfterViewInit } from "@angular/core";
import { DataService } from '../share/data.service';
import { donhang } from '../models/donhang';
import { Input } from '@angular/core/src/metadata/directives';
import { UploadService } from '../share/upload.service';
import { upload } from '../models/upload';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import {Location} from '@angular/common';

//import { request } from 'https';
@Component({
  selector: `app-sign`,
  templateUrl: './sign.component.html',
  styleUrls:['./sign.component.css']
})
export class SignComponent implements AfterViewInit {
 // @Input() donhang: donhang;
  donhangs: donhang[];
  id ="";
  context: CanvasRenderingContext2D;
  dragging = false;
  width = window.innerWidth;
  height = window.innerHeight;
  
  file: File;
  upload: upload;


  @ViewChild("myCanvas") myCanvas;
  // @ViewChild("canvasImg") canvasImg;
  constructor(private dataService: DataService, private uploadService: UploadService,private route: ActivatedRoute, private location: Location) {
    this.dataService.getDonhangs().subscribe(donhangs => {
      this.donhangs = donhangs;
    });
    this.route.paramMap.subscribe((param:ParamMap)=>{
      this.id = param.get('id');
      console.log('id',this.id);
      return this.id;
    });
  //  console.log(this.donhang.id);
  }
  ngAfterViewInit() {
    let canvas = this.myCanvas.nativeElement;
    this.context = canvas.getContext("2d");
    // this.tick();
    //console.log("data",canvas.todataURL())
  }

  tick = function (event) {
    //console.log("event",event);
    var ctx = this.context;
    ctx.lineWidth = 1.8;
    if (this.dragging) {
      // console.log(event.touches[0].clientX );
      // console.log( event.touches[0].clientY-60);
      // ctx.clearRect(0, 0, 400, 400);
      // ctx.fillStyle = this.rectColor;
      // ctx.fillRect(0, 0, this.rectW, this.rectH);
      ctx.lineTo(event.touches[0].clientX, event.touches[0].clientY - 80);
      ctx.stroke();
      ctx.beginPath();
      //ctx.fillRect(event.offsetX, event.offsetY, 10,  Math.PI*2);
      ctx.fillRect(event.touches[0].clientX, event.touches[0].clientY - 80, 0.5, 0.5);
      //ctx.arc(event.touches[0].clientX, event.touches[0].clientY, 10, 0, Math.PI*2);
      ctx.fillStyle = "black";
      ctx.beginPath();
      ctx.moveTo(event.touches[0].clientX, event.touches[0].clientY - 80);
      //console.log ("tick",this.context);
    }


  }

  engage = function (event) {
    // requestAnimationFrame(()=>{
    //   this.engage(event);
    // });
    console.log("event", event.touches[0].clientX);
    console.log("engage");
    this.dragging = true;
    this.tick(event);

  }

  disengage = function () {
    var ctx = this.context;
    this.dragging = false;
    ctx.beginPath();

  }

  uploadFile(f: File){

    const fileToUpLoad = f;
    this.upload = new upload(fileToUpLoad);
    this.uploadService.uploadFile(this.upload);
  }

  savesign() {

    //   var data = canvas.todataURL();
    //       console.log("data", data)
    // }
    var ctx = this.context;
    //var imgData = ctx.getImageData(0, 0, this.width, this.height);
   // ctx.putImageData(imgData, 1, 70);
   // console.log("id",id);
    //this.donhangs[0].chuky = ;
    this.dataService.updateDh(this.donhangs[0]);

    let dataUrl = (<HTMLCanvasElement>this.myCanvas.nativeElement).toDataURL('image/png', 0.7);//.replace("image/png", "image/octet-stream"); 
    console.log(dataUrl);
    var f:File = this.b64toFile(dataUrl, this.id);
    console.log(f);
    this.uploadFile(f);

    for(let i=0; i<this.donhangs.length; i++){
      if(this.donhangs[i].id == this.id){
        console.log('donhang', this.donhangs[i]);
        this.donhangs[i].trangthai = "Đã giao";
        this.dataService.updateDh(this.donhangs[i]);
      }
    }
    this.back();
     //window.location.reload();
    // fetch(dataUrl)
    //   .then(res => res.blob())
    //   .then(blob => {
    //     var fd = new FormData()
    //     fd.append('image', blob, 'filename');
    //      console.log(blob);
    //     console.log(fd)
    //     return <File>blob;
       


    //   });

  }
  back(){
    this.location.back();
  }
  clearsign(){
    var ctx = this.context;
    ctx.clearRect(0, 0, this.width, this.height-20);
  }

  b64toFile(dataURI, id): File {
    // convert the data URL to a byte string
    const byteString = atob(dataURI.split(',')[1]);

    // pull out the mime type from the data URL
    const mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0]

    // Convert to byte array
    const ab = new ArrayBuffer(byteString.length);
    const ia = new Uint8Array(ab);
    for (let i = 0; i < byteString.length; i++) {
      ia[i] = byteString.charCodeAt(i);
    }

    // Create a blob that looks like a file.
    const blob = new Blob([ab], { 'type': mimeString });
    blob['lastModifiedDate'] = (new Date()).toISOString();
    blob['name'] = id;

    // Figure out what extension the file should have
    switch (blob.type) {
      case 'image/jpeg':
        blob['name'] += '.jpg';
        break;
      case 'image/png':
        blob['name'] += '.png';
        break;
    }
    // cast to a File
    return <File>blob;
  }
}




