import { Component } from '@angular/core';
import {  OnInit } from '@angular/core';
import {   MatFormFieldControl} from '@angular/material';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { DataService } from './share/data.service';
import { ReversePipe } from './share/reverse.pipe';
import { donhang } from './models/donhang';
import { bk } from './models/bk';
import { mathang } from './models/mathang';
import {Location} from '@angular/common';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  filters = [];
    state = "Tất cả";
    donhangs: donhang[];
    bk: bk[];
    mathang: mathang[];
    mh: mathang;
    constructor(private route: ActivatedRoute, private dataService: DataService, private _location: Location) {
      this.filters= this.dataService.filters;  
      this.dataService.getDonhangs().subscribe(donhangs => {
        this.donhangs = donhangs;
        this.dataService.countState(this.donhangs, this.donhangs.length);
        
       //console.log("viewdonhang",this.donhangs[0].ngaygiao.getDate().toString());
      });
  
      this.dataService.getBks().subscribe(bks => {
        this.bk = bks;      
       // console.log("viewdonhang-bk",this.bk);
      });
   
    }
  
    ngOnInit() {
  
  //     this.route.paramMap.subscribe((param: ParamMap) => {
  //       return this.state = param.get('state');
  //     });
  // console.log("state", this.state);
    }
   
 
    backClicked() {
      this._location.back();
  }
  
  filterstate(filter){
    this.state = filter.name;
  }
  
}



