import { Injectable } from '@angular/core';

import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';
import { firestore, storage } from 'firebase/app';
import { donhang } from '../models/donhang';
import { bk } from '../models/bk';
import { mathang } from '../models/mathang';
import { xe } from '../models/xe';
import * as firebase from 'firebase';
import { AngularFireModule } from 'angularfire2';
import { upload} from '../models/upload';
import { sign } from '../models/sign';
@Injectable()

export class UploadService {

    basePath = '/uploads';
   // upload: Observable<donhang>;
    constructor(private ngFile: AngularFireModule,
        private db: AngularFireDatabase) {

    }
    uploadFile(upload: upload) {
        const storageRef = firebase.storage().ref();
        const uploadTask = storageRef.child(`${this.basePath}/${upload.file.name}`)
            .put(upload.file);
        uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED),
            (snapshot) => {
                upload.progress = (uploadTask.snapshot.bytesTransferred / uploadTask.snapshot.totalBytes) * 100;
                console.log(upload.progress);
            },
            (error) => {
                console.log(error);
            },
            (): any => {
                upload.url = uploadTask.snapshot.downloadURL;
                upload.name = upload.file.name;
                this.saveFile(upload);
            }
    };
    private saveFile(upload: upload) {
        this.db.list(`${this.basePath}/`).push(upload);
        console.log("File save: "+ upload.url);
    };

    getImages(name: string, src:string) {
        var storage = firebase.storage();
        var ref = "uploads/"+name+".png";
      var bkRef = storage.ref(ref).getDownloadURL()
      .then(function(url){
        console.log(url);
        return src = url ;
         
      }).catch(function(error){
          console.log(error);
      });
      return src;
      
      };

}



