import { Component, OnInit } from '@angular/core';
import { donhang } from '../models/donhang';
import { DataService } from '../share/data.service';

import { DatePipe } from '@angular/common';
import { mathang } from '../models/mathang';
import { bk } from '../models/bk';
import { khachhang } from '../models/khachhang';
import { xe } from '../models/xe';
@Component({
  selector: 'app-ta-don-hang',
  templateUrl: './ta-don-hang.component.html',
  styleUrls: ['./ta-don-hang.component.css']
})
export class TaDonHangComponent implements OnInit {
  xe: xe[];
  statebk: boolean;
  donhangs: donhang[];
  soluong: number;
  tenhang: string;
  lengthdh: number;
  bienso: string;
  laixe: string;
  ngaygiao: Date;
  thanhtoan: number;
  dc: string;
  sdt: string;
  tenkh: string;
  donhang: donhang;
  now: Date = new Date();
  mhs = [{ tenhang: 'Cát VP', dvt: 'm3' },
  { tenhang: 'Đá 1/2', dvt: 'm3' },
  { tenhang: 'Cát đen', dvt: 'm3' },
  { tenhang: 'Đá mạt', dvt: 'm3' }];
  items = [];
  item = { tenhang: "", soluong: 0, dvt: "m3" };
  constructor(private dataService: DataService) {
    this.dataService.getDonhangs().subscribe(d => {
      this.lengthdh = d.length;

    });
    this.dataService.getXes().subscribe(x => {
      this.xe = x;
      console.log("xes", this.xe);
    });
  }

  ngOnInit() {
  }
  addTemp() {
    if (this.tenhang != "" && this.soluong != 0) {

      this.item.tenhang = this.tenhang;
      this.item.dvt = "m3";
      this.item.soluong = this.soluong;

      this.items.push(this.item);

      this.item = { tenhang: "", soluong: null, dvt: "m3" };
      console.log('items', this.items);
      this.tenhang = "";
      this.soluong = null;
    }
  }

  taodon() {
if (this.tenkh != "" && this.sdt != "" && this.dc != "") {
   
      var i = this.lengthdh + 1;
      var kh: khachhang = { tenkh: this.tenkh, sdt: this.sdt, dc: this.dc };
      var x: xe;
      for (let i = 0; i < this.xe.length; i++) {
        if (this.xe[i].bienso == this.bienso) { x = this.xe[i]; }
        else {
          console.log("xe - k");
          x= {bienso: this.bienso, taitrong:0};
        }
      };
console.log("gan dh");
      var donhang: donhang = {
        id: "bk" + i, kh: kh, trangthai: "Chưa giao", thanhtoan: this.thanhtoan == null ? 0 : this.thanhtoan,
        tong: 0, nolai: 0, ngaytao: this.now, ngaygiao: this.ngaygiao, laixe: this.laixe, xe: x
      };
    console.log("add dh");
      this.dataService.addDonhang(donhang);
    }

    for (let i = 0; i < this.items.length; i++) {
      console.log("add bk");
      var mh: mathang;
      mh = { tenhang: this.items[i].tenhang, dvt: this.items[i].dvt, dongia: 0 };
      var it: bk = { id: donhang.id, mathang: mh, soluong: this.items[i].soluong, thanhtien: 0 };
      this.dataService.addBk(it);

    }
    this.dataService.getDonhangs().subscribe(donhangs => {
      this.donhangs = donhangs;
      this.dataService.countState(this.donhangs, this.donhangs.length);
      
     //console.log("viewdonhang",this.donhangs[0].ngaygiao.getDate().toString());
    });
  }


updateBk(ind: number) {
  console.log(ind);
  this.statebk = false;
  this.tenhang = this.items[ind].tenhang;
  this.soluong = this.items[ind].soluong;
  this.items.splice(ind, 1);


}

removeBk(index) {
  //console.log(this.bks[index]);
  // var bktemp: bk = this.bks[index];

  this.items.splice(index, 1);

}

}



