import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from 'angularfire2/firestore';
import { Observable } from 'rxjs/Observable';
import { firestore } from 'firebase/app';
import { donhang } from '../models/donhang';
import { bk } from '../models/bk';
import { mathang } from '../models/mathang';
import { xe } from '../models/xe';
@Injectable()

export class DataService {


  filters = [
    { name: "Tất cả", count: 0 },
    { name: "Chưa giao", count: 0 },
    { name: "Đã giao", count: 0 },
    { name: "Đang bốc hàng", count: 0 },
    { name: "Đang đi giao", count: 0 },
    { name: "Đơn bị hủy", count: 0 }
  ];

  donhang: AngularFirestoreCollection<donhang>;
  donhangs: Observable<donhang[]>;
  donhangDoc: AngularFirestoreDocument<donhang>;


  bk: AngularFirestoreCollection<bk>;
  bks: Observable<bk[]>;
  bkDoc: AngularFirestoreDocument<bk>;

  mathang: AngularFirestoreCollection<mathang>;
  mathangs: Observable<mathang[]>;
  mathangDoc: AngularFirestoreDocument<mathang>;

  xe: AngularFirestoreCollection<xe>;
  xes: Observable<xe[]>;

  constructor(public afs: AngularFirestore) {

  }
  ngOninit() { };
  getDonhangs() {
    //console.log("getdonhang");
    this.donhang = this.afs.collection<donhang>('donhangs');
    this.donhangs = this.donhang.snapshotChanges().map(changes => {
    //  console.log("change");
      return changes.map(a => {
        const data = a.payload.doc.data() as donhang;
        data.ma = a.payload.doc.id;
       // console.log("donhangs");
        return data;
      })
    });
    return this.donhangs;
  };

  getBks() {
    this.bk = this.afs.collection<bk>('bks');
    this.bks = this.bk.snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as bk;
        data.ma = a.payload.doc.id;
        return data;

      })
    });

    return this.bks;
  };

  getBk(id: string) {
    this.bk = this.afs.collection<bk>('bks', ref => ref.where('id', '==', id));
    this.bks = this.bk.snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as bk;
        data.ma = a.payload.doc.id;
        return data;
      })
    });
   // console.log("getdonhang", this.bks);
    return this.bks;

  }

  getMathangs() {
    this.mathangs = this.afs.collection('mathang').snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as mathang;
        return data;
      })
    });
    return this.mathangs;
  };

  getXes() {
    this.xes = this.afs.collection('xe').snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as xe;

        return data;
      })
    });
    return this.xes;
  };

  addDonhang(dh: donhang) {
    this.donhang.add(dh);
  };
  updateDh(dh: donhang){
    this.donhangDoc = this.afs.doc(`donhangs/${dh.ma}`);
    console.log("bkDoc", this.donhangDoc);
    this.donhangDoc.update(dh);
  };
  addBk(bangke: bk) {
    console.log("bk", this.bk);
    console.log("bangke", bangke);
    
    this.bk.add(bangke);
  };
  addBks(bangke: bk[]) {
    for(let i = 0 ; i<bangke.length; i++){
    this.bk.add(bangke[i]);
    }
  };
  removeBk(bangke: bk) {
    console.log("remove", bangke);
    this.bkDoc = this.afs.doc(`bks/${bangke.ma}`);
    console.log("bkDoc", this.bkDoc);
    this.bkDoc.delete();
    console.log("delete");
  };

  removeBks(id:string, bke: bk[]) {
    console.log("xóa");
    var lgth:number;
    var bangke:bk[];
    
      for(let i=0 ; i<bke.length; i++){
        console.log("for",i);
        
          console.log("ma", bke[i].ma);
        this.bkDoc = this.afs.doc(`bks/${bke[i].ma}`);
        
         this.bkDoc.delete();
        
      }
 
  };
 
 
  updatebk(bangke: bk) {
    this.bkDoc = this.afs.doc(`bks/${bangke.ma}`);
    this.bkDoc.update(bangke);
   
  }

  counts(state, donhangs:donhang[], length) {

    var cnt = 0;
    for (let i = 0; i < length; i++) {
      if (donhangs[i].trangthai == state) {
        cnt++;
      }
    }
    return cnt;
  };
  countState(donhangs:donhang[], length) {
    console.log(length);
    this.filters[0].count = length;
    for (let index = 1; index < this.filters.length; index++) {
      this.filters[index].count = this.counts(this.filters[index].name, donhangs, length);
    }
  };
 
}




